import React, {createRef, useEffect, useRef, useState} from "react";
import {connect} from 'react-redux';
import PageTitle from "../common/PageTitle";
import Menu from "../menu/Menu";
import PageHeading from "../common/PageHeading";
import {setBg} from "../../actions/UIActions";
import Grain from "../common/Grain";

import Footer from "../common/Footer";
import pressList from "./press-list";
import PressItem from "./PressItem";
import {changeCursorSize} from "../../actions/CursorActions";
import {MENU_CURSOR_SIZE, DEFAULT_CURSOR_SIZE} from "../../constants/cursorConstants";

function Press(props) {

    const inputRef = useRef();

    const pressDates = [...new Set(pressList.map(a => a.date))];
    const pressFlag = Object.fromEntries(pressDates.map( date => [date,false]));
    const pressDatesRefs = useRef(Object.fromEntries(pressDates.map( date => [date,createRef()])));
    const pressElems = pressList.map((item, index) => {
        if(pressFlag[item.date] === false) {
            pressFlag[item.date] = true;
            return <PressItem data={item} innerRef={pressDatesRefs.current[item.date]} index={index}/>
        }
        else
            return <PressItem data={item} index={index}/>
    })

    const [left, setLeft] = useState(0);

    useEffect(()=> {
        props.dispatch(setBg("#c52137"));
        console.log(pressDatesRefs);
    },[]);

    const scrollHandler = (e) => {
      setLeft(2*inputRef.current.scrollLeft);
    };

    return (
        <>
            <PageTitle title="Press | Ashish Avikunthak"/>
            <div>
                <Menu></Menu>
                <div style={{
                    width: "100%",
                    display: "flex",
                    alignItems: "center",
                    flexDirection: "column",
                }}
                >
                    <PageHeading title={"Press"} style={{
                        color: "#ffffff"
                    }}/>
                    <div
                        className={'press-dates'}
                    >
                        {pressDates.map((date) =>
                            <div
                                onMouseEnter={() => {
                                    props.dispatch(changeCursorSize(MENU_CURSOR_SIZE))
                                }}
                                onMouseLeave={() => {
                                    props.dispatch(changeCursorSize(DEFAULT_CURSOR_SIZE))
                                }}
                                onClick={() => {
                                    inputRef.current.scrollLeft =
                                        pressDatesRefs.current[date].current.offsetLeft - pressDatesRefs.current[date].current.offsetWidth;;
                                }}
                            >
                                {date}
                            </div>
                        )}
                    </div>
                    <div
                        className={'press-film-strip'}
                        style={{
                            backgroundPosition: -left + "px 0",
                        }}
                    >

                    </div>
                    <div
                        ref={inputRef}
                        className={'press'}
                        onScroll={scrollHandler}>
                        <div className={'press-items-holder'}>
                            {pressElems}
                        </div>
                    </div>
                </div>
            </div>
            <Footer/>
            <Grain opacity={0.1}/>
        </>
    );
}

function mapStateToProps(state) {
    return {
        size: state.cursorReducer.cursorSize
    };
}

export default connect(mapStateToProps)(Press);