import React, {useEffect} from "react";
import {connect} from 'react-redux';

import {changeCursorSize} from "../../actions/CursorActions";
import {DEFAULT_CURSOR_SIZE, BIG_CURSOR_SIZE} from "../../constants/cursorConstants";

function PressItem(props) {

    const item = props.data;
    return (
            <div ref={props.innerRef} className={'press-details'}>
                <b onClick={(e) => {
                    console.log("hello",e.target.parentNode.parentNode.scrollLeft);
                            e.target.parentNode.parentNode.parentNode.scrollLeft = e.target.offsetLeft - e.target.parentNode.offsetWidth;
                    }
                }>{item.date}</b>
                <div>
                    <div>
                        {
                            item.link &&
                            <a
                                href={item.link}
                                target={"_blank"}
                                onMouseEnter={() => {
                                    props.dispatch(changeCursorSize(BIG_CURSOR_SIZE))
                                }}
                                onMouseLeave={() => {
                                    props.dispatch(changeCursorSize(DEFAULT_CURSOR_SIZE))
                                }}
                            >
                                {item.title}
                            </a>
                        }
                        {
                            !item.link &&
                            item.title
                        }
                    </div>
                </div>
            </div>
    );
}

function mapStateToProps(state) {
    return {
        size: state.cursorReducer.cursorSize
    };
}

export default connect(mapStateToProps)(PressItem);